








































































import Vue from 'vue';
import { mapGetters, mapMutations, mapActions } from 'vuex';
import ContactMe from '@/components/ContactMe.vue';

export default Vue.extend({
  components: {
    ContactMe,
  },
  computed: {},
  methods: {
    onScroll(e: Event) {
      if (typeof window === 'undefined') return;
      const top = window.pageYOffset || (<HTMLElement>e.target).scrollTop || 0;
      this.showToTop = top > 20;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
    ...mapActions({ signout: 'auth/signout' }),
  },
  data() {
    return {
      drawer: false,
      fixed: false,
      clipped: false,
      items: [
        {
          icon: 'mdi-human',
          title: 'Professional',
          to: '/professional',
        },
      ],
      right: true,
      showToTop: false,
      title: 'Marvin Cai',
    };
  },
});
